.button {
    max-width: 340px;
    height: 45px;
    background: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    font-weight: bold;
    box-shadow: 5px 5px 5px black;
    border-radius: 10px;
}